.profile {
  border-radius: 16px;
  border: 2px ridge rgb(110, 154, 110);
  padding-top: 60px;
  background-color: #f8ffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  -webkit-box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
  -moz-box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
  box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
}

.description {
  padding-left: 4px;
  padding-right: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding-bottom: 20px;
}

.avatar {
  text-align: center;
  padding-top: 8px;
  display: block;
  width: 100%;
  max-width: 200px;
  height: auto;
  border-radius: 50%;
  border: 2px ridge rgba(94, 192, 151, 0.3);
  -webkit-box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
  -moz-box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
  box-shadow: 1px 1px 59px 9px rgba(99, 239, 97, 0.86) inset;
}

.name {
  text-align: center;
  font-family: Roboto;
  font-size: 38px;
  font-weight: 600;
  line-height: 1.2;
  padding-top: 40px;
  color: rgb(52, 51, 51);
}
.location,
.tag {
  text-align: center;
  font-family: Ubuntu;
  font-size: 24px;
  font-weight: 100;
  line-height: 1.2;
  color: rgb(78, 74, 74);
}

.stats {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.statsItem {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border: 2px ridge rgb(68, 196, 68);
  -webkit-box-shadow: 1px 1px 19px 6px rgba(115, 226, 97, 0.51) inset;
  -moz-box-shadow: 1px 1px 19px 6px rgba(115, 226, 97, 0.51) inset;
  box-shadow: 1px 1px 19px 6px rgba(115, 226, 97, 0.51) inset;
  border-radius: 16px;
}
.stats:nth-child(3) {
  margin-left: 0;
}
.label {
  text-align: center;
  font-family: Ubuntu;
  font-size: 16px;
  font-weight: 100;
  line-height: 1.1;
  color: rgb(78, 74, 74);
  padding-top: 40px;
}
.quantity {
  font-family: Ubuntu;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.2;
  color: rgb(52, 51, 51);
  text-align: center;
  padding-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .profile {
    padding-top: 60px;
  }
  .avatar {
    max-width: 400px;
  }

  .name {
    font-size: 60px;
  }

  .location,
  .tag {
    font-size: 36px;
  }

  .stats {
    justify-content: center;
  }

  .label {
    font-size: 22px;
  }
  .quantity {
    font-size: 36px;
  }
}

@media screen and (min-width: 1200px) {
  .profile {
    padding-top: 80px;
  }

  .description {
    padding-left: 10px;
    padding-right: 10px;
    gap: 20px;
    padding-bottom: 60px;
  }

  .name {
    font-size: 80px;
  }
  .tag,
  .location {
    font-size: 58px;
  }
  .label {
    font-size: 38px;
  }

  .quantity {
    font-size: 48px;
  }
}
