.section {
  background-color: rgb(164, 223, 203);
  padding-bottom: 40px;
}

.container {
  max-width: 375px;
  min-width: 300px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: auto;
  margin-right: auto;
}

.profileContainer {
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 44px;
    padding-right: 44px;
  }
}
